
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'

import axios from 'axios'
export default {
name: 'userpage',
components: {
XHeader,
Card
},
data () {
return {
authcheck: false,
username: '',
birthday: '',
statments: []
}
},
beforeRouteEnter (to, from, next) {
axios.get('api/check')
.then(function (response) {
next(vm => {
vm.authcheck = response.data.authcheck
vm.username = response.data.username
vm.birthday = response.data.birthday
vm.statments = response.data.statments
vm.login_state = true
})
})
.catch(function () {
next('/login')
})
},
methods: {
logout: function () {
let that = this.$router
axios.get('/logout')
.then(function () {
that.push('/demo')
})
}
}
}



