
import XHeader from 'vux/src/components/x-header/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

import axios from 'axios'
export default {
name: 'TodoList',
components: {
XButton,
Toast,
CheckIcon,
XHeader,
XInput,
Group
},
data () {
return {
username: '',
password: '',
show: false,
rememberme: false
}
},
methods: {
submit: function () {
let formdata = new FormData()
let that = this
formdata.append('email', this.username)
formdata.append('password', this.password)
formdata.append('remember_me', this.rememberme)
axios({
method: 'post',
url: '/login',
data: formdata,
config: {
header: {'Content-Type': 'multipart/form-data'}
}
})
.then(function () {
that.$router.push('/userpage')
}).catch(function () {
that.show = true
that.username = ''
that.password = ''
that.remember_me = false
setTimeout(() => {
that.show = false
}, 1000)
})
}
},
beforeRouteEnter (to, from, next) {
axios.get('api/check')
.then(function (response) {
next('userpage')
})
.catch(function () {
next()
})
}
}



